@font-face {
  font-family: "PT mono";
  font-style: normal;
  font-weight: 400;
  src:
    url("../fonts/pt-mono-400.woff2") format("woff2"),
    url("../fonts/pt-mono-400.woff") format("woff");
  font-display: swap;
}

